<template>
	<div id="openWechatPay">
		<!---------------isPremission==false------------------->
		<div class="noPremission" v-if="isPremission==false">
			<div class="text left">
				<p class="bigTop"><span class="blue">0755-2306 0180</span> </p>
				
				<p></p>
				<p>1.</p>
				<p>2.</p>
				<p>3.1. 2. 3. </p>
				<p>2Mbmppngjpegjpggif</p>
				<p>4.</p>
			</div>
		</div>

		<!---->
		<div  class="formConfig" v-else>
			<el-form label-width="155px" :model="noConfigForm" label-position="right" ref="noConfigForm" :rules="rules">
				<el-form-item label="">
					<span>{{noConfigForm.shopNo}}</span>
				</el-form-item>
				<el-form-item label="API">
					<span>{{noConfigForm.passWord}}</span>
				</el-form-item>
				<el-form-item label="">
					<span></span>
				</el-form-item>
				<el-form-item label="" prop="imgSrc">
					<div class="img left" v-if="isImg">
						<div class="mask"></div>
						<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+noConfigForm.imgSrc" alt="" />
						<i class="el-icon-view" @click='clickImg'></i>
					</div>
					<code-img v-if="showImg" @clickit="viewImg" :imgSrc="noConfigForm.imgSrc"></code-img>
					<img-upload @send-image='getPhoto'></img-upload>
					<div class="text-warp">
						<p class="small"></p>
					</div>
				</el-form-item>
				<div class="save">
					<el-button @click="silverSetSave('noConfigForm')"></el-button>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
	import ImgUpload from '@/components/upload/SUpload';
	import codeImg from "../../components/showCodeImg";
	import { silverData } from "../../api";
	import { silverMethods } from "../../index";
	import { isSpecial, silverCode } from "@/config/cfg"
	import { ls } from "@/config/pageStore";
	import { Public } from "@/config/public";
	export default {
		props: ["config"],
		data() {
			return {
				imgbase_url: imgbase_url, //
				isImg: false,
				showImg: false,
				wConfigInfo: {},
				isPremission: false,
				meta: {
					keepAlive: true
				},
				noConfigForm: {
					shopNo: "12345678", //
					passWord: "1233456", //API
					imgSrc: '', //
					contributing: "" //

				},
				rules: {
					imgSrc: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
				}
			}
		},
		created() {
			this.shopId = Public.getShopid();
			this.initData()
			this.isPremission = this.config;
		},
		components: {
			ImgUpload,
			codeImg,
		},
		methods: {
			//
			initData() {
				let data = {
					select_weixin_set: 1,
					shop_id: this.shopId,
					pay_way: 2
				}
				silverData.getShopData(data, (resp) => {
					this.noConfigForm.shopNo = resp.data.weixin_pay_set==undefined||resp.data.weixin_pay_set.length==0?"":resp.data.weixin_pay_set[silverCode.NO].sub_mch_id;
					this.noConfigForm.passWord = resp.data.weixin_pay_set==undefined||resp.data.weixin_pay_set.length==0?"":resp.data.weixin_pay_set[silverCode.NO].api_key;
					this.noConfigForm.contributing =resp.data.weixin_pay_set==undefined||resp.data.weixin_pay_set.length==0?"": (resp.data.weixin_pay_set[silverCode.NO].spc_sub == isSpecial.YES ? isSpecial.code[isSpecial.YES] : isSpecial.code[isSpecial.NO]);
					this.noConfigForm.imgSrc = resp.data.weixin_pay_set==undefined||resp.data.weixin_pay_set.length==0?"":resp.data.weixin_pay_set[silverCode.NO].tenpay_img;
					if(this.noConfigForm.imgSrc !== "") {
						this.isImg = true;
					}
				})
			},
			getPhoto(res) {
				this.noConfigForm.imgSrc = res;
				this.isImg = true;
			},
			clickImg() {
				this.showImg = true;
				// 
				this.noConfigForm.imgSrc = this.noConfigForm.imgSrc
			},
			viewImg() {
				this.showImg = false;
			},
			silverSetSave(formName) {
				console.log(this.noConfigForm.imgSrc)
				let data = {
					save_weixin_pay_set: 1, //
					pay_way: 2, // (int)1:,2:
					shop_id: this.shopId, //(string)id
					tenpay_img: this.noConfigForm.imgSrc // (string)          //pay_way=2
				}
				silverMethods.saveSeting(data, this.$refs[formName], this.$notify)
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#openWechatPay{
		margin-left: -10px;
	}
	.img {
		width: 50px;
		height: 50px;
		margin-right: 30px;
	}
	
	img {
		width: 100%;
		height: 100%;
	}
	
	.text {
		width: 80%;
		margin-left: 35px;
	}
	
	p {
		font-size: 12px;
		color: #9B9B9B;
		height: 30px;
		line-height: 30px;
	}
	
	.small {
		font-size: 12px;
		color: darkgray;
	}
	
	.blue {
		font-size:12px;
		 color: #4877E7;;
	}
	
	.bigTop {
		height: 50px;
		line-height: 50px;
	}
	
	.notConfig {
		width: 60%;
	}
	
	.img {
		width: 100px;
		height: 100px;
		margin-right: 20px;
		position: relative;
		img {
			width: 100%;
			height: 100%;
		}
	}
	
	.save {
		width: 100%;
		background: #FFFFFF;
		margin-top: 100px;
		text-align: center;
	}
	
	.img:hover .mask,
	.img:hover .el-icon-view {
		display: block;
	}
	
	.mask {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.el-icon-view {
		display: none;
		position: absolute;
		opacity: 1;
		top: 40%;
		left: 40%;
		font-size: 20px;
		color: #fff;
		cursor: pointer;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#openWechatPay {
		.el-radio.yes {
			margin-right: 100px;
		}
		.el-button.submitBtn {
			margin: 0 30px 0 200px;
		}
		.el-button.cancelBtn {
			background: #fff;
			color: #4877E7;
		}
		.el-button.downLoad {
			background: #fff;
			color: #666666;
			border: 1px solid #666666;
		}
		.el-form-item__label {
			text-align: right;
			/*padding-left: 0;*/
			
			padding-right: 47px;
		}
		.el-input {
			width: 300px;
		}
		.formConfig .el-form {
			margin-left: -30px;
			padding-left: 0;
		}
	}
</style>


