<template>
	<div id="alipaySet">
		<!--<div class="alipay-title"><span></span><span>></span><span @click="backToSilver" class="blue"></span><span>></span><span class="blue-txt"></span></div>-->
		<!--<div class="form-card-title"></div>-->
		<div class="form-card">
			<div class="form_title"></div>
			<el-form label-width="110px" :model="form" :rules="rules" ref="form">
				<el-form-item label="" prop="account">
					<el-radio-group v-model="form.account" @change="changeProceeds('form')">
						<el-radio v-for="item in alipay" :label="item.label" :key="item.id"></el-radio>
					</el-radio-group>
				</el-form-item>

				<div v-if="isOpenAlipay">
					<el-form-item label="" prop="imgSrc">
						<div class="img left" v-if="isImg">
							<div class="mask"></div>
							<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+form.imgSrc" alt="" />
							<i class="el-icon-view" @click='clickImg'></i>
						</div>

						<code-img v-if="showImg" @clickit="viewImg" :imgSrc="form.imgSrc"></code-img>

						<img-upload @send-image='getPhoto' class="left"></img-upload>
						<div class="text-warp left">
							<p class="text">--</p>
						</div>
					</el-form-item>
					 <!-- <el-form-item label="" prop="samllChangeType">
						 <el-radio-group v-model="form.samllChangeType" @change="changeShowCode('form')">
							<el-radio label=''></el-radio>
							<el-radio label=''></el-radio>
						</el-radio-group>
					</el-form-item> -->
					<div class="save">
						<el-button @click="silverSetSave('form')"></el-button>
					</div>
				</div>
				<div v-else>
					<alipay :config="isConfig"></alipay>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
	import ImgUpload from '@/components/upload/SUpload';
	import codeImg from "../components/showCodeImg";
	import alipay from "./children/openAlipay";
	import { silverData } from "../api";
	import { silverMethods } from "../index";
	import { Public } from "@/config/public";
	import { silverCode } from "@/config/cfg";
	export default {
		data() {
			return {
				imgbase_url: imgbase_url, //
				shopId: "",
				codeStatus: "",
				isConfig: true,
				isImg: false,
				showImg: false,
				isOpenAlipay: true,
				form: {
					account: "",
					samllChangeType: "",
					imgSrc: ""
				},
				alipay: [{
						label: "",
						id: 1
					},
					{
						label: "",
						id: 2
					}
				],
				showCode: [{
					label: ""
				}, {
					label: ""
				}],
				rules: {
					account: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					samllChangeType: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					imgSrc: [{
						required: true,
						message: '',
						trigger: 'click'
					}],
				}

			}
		},
		created() {
			this.shopId = Public.getShopid();
			this.getShopData();
//			sessionStorage.setItem("index",3)
		},
		components: {
			ImgUpload,
			codeImg,
			alipay
		},
		methods: {
			//
			getShopData() {
				let data = {
					select_alipay_set: 1,
					shop_id: this.shopId,
					pay_way: 1
				}
				silverData.getShopData(data, (resp) => {
					this.isConfig =(resp.data.alipay_seting==undefined||resp.data.alipay_seting==[])|| resp.data.alipay_seting == silverCode.NO ? false : true;
					let index = sessionStorage.getItem("isOpenAlipay");
					if(index !== null) {
						this.form.account = index
					} else {
					this.form.account = resp.data.alipay==undefined&&resp.data.alipay[silverCode.NO]==undefined?"":(resp.data.alipay[silverCode.NO]==undefined||resp.data.alipay[silverCode.NO].pay_way == silverCode.YES ? this.alipay[silverCode.NO].label : this.alipay[silverCode.YES].label);
					}
					this.form.samllChangeType = resp.data.alipay[silverCode.NO]==undefined|| resp.data.alipay[silverCode.NO].code_show == silverCode.YES ? this.showCode[silverCode.NO].label : this.showCode[silverCode.YES].label;
					this.form.imgSrc = resp.data.alipay[silverCode.NO]==undefined?"": resp.data.alipay[silverCode.NO].code_img;
					if(this.form.imgSrc !== "") {
						this.isImg = true;
					}

				})
			},
			getPhoto(res) {
				this.form.imgSrc = res;
				this.isImg = true;
			},
			clickImg() {
				this.showImg = true;
				// 
				this.form.imgSrc = this.form.imgSrc
			},
			viewImg() {
				this.showImg = false;
			},
			changeProceeds(formName) {
				if(this.form.account !== this.alipay[0].label) {
					this.isOpenAlipay = false;
					this.getShopData()
				} else {
					this.isOpenAlipay = true;
					this.getShopData()
				}
			sessionStorage.setItem("isOpenAlipay", this.form.account)
			},

			//
			changeShowCode(formName) {
				this.codeStatus = this.form.showPayCode !== undefined && this.form.showPayCode == silverCode.code[silverCode.NO] ? silverCode.NO : silverCode.YES;
			},
			//
			isReadySend(formName) {
				let data = {
					save_alipay_set: 1, //
					pay_way: 1, // (int)1:,2:
					shop_id: this.shopId, //(string)id,id
					code_img: this.form.imgSrc, // (string) 
					code_show: this.codeStatus // (int)0:,1:)
				}
				silverMethods.saveSeting(data, this.$refs[formName], this.$slnotify)

			},
			//
			silverSetSave(formName) {
				this.isReadySend(formName)
			},
			backToSilver() {
				this.$emit("alipay-click")
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.form-card {
		width: 100%;
		height: 600px;
		background: #FFFFFF;
		padding: 0px 14px 14px 0px;
	}
	
	.text-warp .text {
		margin-left: 15px;
		font-size: 12px;
		color: darkgray;
	}
	
	.img {
		width: 100px;
		height: 100px;
		margin-right: 20px;
		position: relative;
		img {
			width: 100%;
			height: 100%;
		}
	}
	
	.img:hover .mask,
	.img:hover .el-icon-view {
		display: block;
	}
	
	.mask {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.el-icon-view {
		display: none;
		position: absolute;
		opacity: 1;
		top: 40%;
		left: 40%;
		font-size: 20px;
		color: #fff;
		cursor: pointer;
	}
	.form_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		letter-spacing: 0.5px;
		background-color: #F6F8FC;
		padding-left: 14px;
	}
	.save {
		width: 100%;
		background: #FFFFFF;
		margin-top: 200px;
		text-align: center;
	}
	
	.blue {
		color: #3254A5;
		cursor: pointer;
	}
	
	.blue-txt {
		color: #4877E7;
		cursor: pointer;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#alipaySet {
		.el-form{
			padding-left:70px;
		}
		.el-form-item__label {
			text-align: left;
			padding-right: 30px;
		}
		.el-radio{
			margin-right: 50px;
		}
	}
</style>


