<template>
	<div id="wechatSet">
		<div class="form-card">
			<div class="form_title"></div>
			<el-form label-width="110px" :model="form" :rules="rules" ref="form">
				<el-form-item label="" prop="gatheringWay">
					<el-radio-group v-model="form.gatheringWay" @change="changeProceeds('form')">
						<el-radio v-for="item in gatheringWay" :label="item.label" :key="item.id"></el-radio>
					</el-radio-group>
				</el-form-item>
				<div v-if="isOpenWechatPay">
					<el-form-item label="" prop="imgSrc">
						<div class="img left" v-if="isImg">
							<div class="mask"></div>
							<img :src="imgbase_url+'/img_get.php?img=1&height=100&width=100&imgname='+form.imgSrc" alt="" />
							<i class="el-icon-view" @click='clickImg'></i>
						</div>
						<code-img v-if="showImg" @clickit="viewImg" :imgSrc="form.imgSrc"></code-img>
						<img-upload @send-image='getPhoto' class="left"></img-upload>
						<div class="text-warp left">
							<p class="text">----</p>
						</div>
					</el-form-item>
					<!-- <el-form-item label="" prop="showPayCode">
						<el-radio-group v-model="form.showPayCode" @change="changeShowCode('form')">
							<el-radio label=''></el-radio>
							<el-radio label=''></el-radio>
						</el-radio-group>
					</el-form-item> -->
					<div class="save">
						<el-button @click="silverSetSave('form')"></el-button>
					</div>
				</div>
				<div v-else>
					<keep-alive>
						<wechat-pay :config="isConfig"></wechat-pay>
					</keep-alive>
				</div>
			</el-form>

		</div>
	</div>
</template>

<script>
	import ImgUpload from '@/components/upload/SUpload';
	import codeImg from "../components/showCodeImg";
	import WechatPay from "./children/openWechatPay"
	import { silverData } from "../api";
	import { silverMethods } from "../index";
	import { Public } from "@/config/public";
	import { silverCode } from "@/config/cfg";
	export default {
		//		props: ["wPremissionConfig"],
		data() {
			return {
				isConfig: true,
				imgbase_url: imgbase_url, //
				shopId: "",
				codeStatus: "",
				isImg: false, //
				showImg: false, //
				isOpenWechatPay: true, //
				form: {
					gatheringWay: "", //
					showPayCode: "", //
					imgSrc: '', //
				},
				gatheringWay: [{
						label: "()",
						id: 1
					},
					{
						label: "()",
						id: 2
					}
				],
				showCode: [{
					label: ""
				}, {
					label: ""
				}],
				rules: {
					gatheringWay: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					showPayCode: [{
						required: true,
						message: '',
						trigger: 'change'
					}],
					imgSrc: [{
						required: true,
						message: '',
						trigger: 'click'
					}],
				}
			}
		},
		created() {
			this.shopId = Public.getShopid();
			this.getShopData();
		},
		components: {
			ImgUpload,
			codeImg,
			WechatPay
		},
		methods: {
			//
			getShopData() {
				let data = {
					select_weixin_set: 1,
					shop_id: this.shopId,
					pay_way: 1
				}
				silverData.getShopData(data, (resp) => {
					this.isConfig = resp.data.weixin_seting == silverCode.NO||(resp.data.weixin_seting == undefined||resp.data.weixin_seting == []) ? false : true;
					let index = sessionStorage.getItem("isOpenWechatPay");
					if(index !== null) {
						this.form.gatheringWay = index
					} else {
						this.form.gatheringWay =resp.data.weixin_pay_set[silverCode.NO]==undefined|| resp.data.weixin_pay_set[silverCode.NO].pay_way == silverCode.YES||resp.data.weixin_pay_set[silverCode.NO].pay_way== undefined ? this.gatheringWay[silverCode.NO].label : this.gatheringWay[silverCode.YES].label
					}
					this.isOpenWechatPay = this.form.gatheringWay == this.gatheringWay[silverCode.NO].label ||this.form.gatheringWay==undefined? true : false;
					this.form.imgSrc = resp.data.weixin_pay_set[silverCode.NO]==undefined?"" :resp.data.weixin_pay_set[silverCode.NO].code_img;
					this.form.showPayCode =  resp.data.weixin_pay_set[silverCode.NO]==undefined||resp.data.weixin_pay_set[silverCode.NO].code_show == silverCode.NO ? this.showCode[silverCode.NO].label : this.showCode[silverCode.YES].label;
					if(this.form.imgSrc !== "") {
						this.isImg = true;
					}
				})
			},
			getPhoto(res) {
				this.form.imgSrc = res;
				this.isImg = true;
			},
			//
			clickImg() {
				this.showImg = true;
				// 
				this.form.imgSrc = this.form.imgSrc
			},
			//
			viewImg() {
				this.showImg = false;
			},

			//
			changeProceeds(formName) {
				if(this.form.gatheringWay !== this.gatheringWay[0].label) {
					this.isOpenWechatPay = false;
					this.getShopData()
				} else {
					this.isOpenWechatPay = true;
					this.getShopData()
				}
				sessionStorage.setItem("isOpenWechatPay", this.form.gatheringWay)
			},
			//
			changeShowCode(formName) {
				this.codeStatus = this.form.showPayCode !== undefined && this.form.showPayCode == silverCode.code[silverCode.NO] ? silverCode.NO : silverCode.YES;
			},
			//
			isReadySend(formName) {
				let data = {
					save_weixin_pay_set: 1, //
					pay_way: 1, // (int)1:,2:
					shop_id: this.shopId, //(string)id,id
					code_img: this.form.imgSrc, // (string) 
					code_show: this.codeStatus // (int)0:,1:)
				}
				silverMethods.saveSeting(data, this.$refs[formName], this.$slnotify)
			},
			//
			silverSetSave(formName) {
				this.isReadySend(formName)
			},
			//
			backToSilver() {
				this.$emit("wechat-click")
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.form-card {
		width: 100%;
		height: 600px;
		background: #FFFFFF;
		padding: 0px 14px 14px 0px;
	}
	
	.text-warp .text {
		margin-left: 15px;
		font-size: 12px;
		color: darkgray;
	}
	
	.img {
		width: 100px;
		height: 100px;
		margin-right: 20px;
		position: relative;
		img {
			width: 100%;
			height: 100%;
		}
	}
	
	.img:hover .mask,
	.img:hover .el-icon-view {
		display: block;
	}
	.form_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		letter-spacing: 0.5px;
		background-color: #F6F8FC;
		padding-left: 14px;
	}
	.mask {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.5);
	}
	
	.save {
		width: 100%;
		background: #FFFFFF;
		margin-top: 200px;
		text-align: center;
	}
	
	.el-icon-view {
		display: none;
		position: absolute;
		opacity: 1;
		top: 40%;
		left: 40%;
		font-size: 20px;
		color: #fff;
		cursor: pointer;
	}
	
	.blue {
		color: #3254A5;
		cursor: pointer;
	}
	
	.blue-txt {
		color: #4877E7;
		cursor: pointer;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#wechatSet {
		.el-form{
			padding-left:70px;
		}
		.el-form-item__label {
			text-align: left;
			padding-right: 30px;
		}
		.el-radio{
			margin-right: 50px;
		}
		.el-radio-group {
			width:160%
		}
	}
</style>


