<template>
	<div id="silver-admin">
		<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
			<el-menu-item index="1"></el-menu-item>
			<el-menu-item index="2"></el-menu-item>
			<el-menu-item index="3"></el-menu-item>
		</el-menu>
		<div v-if="isOpenSliver">
			<div class="form-card">

				<div class="form_title"></div>
				<el-form label-width="180px" :model="form">
					<el-form-item label="">
						<el-radio-group v-model="form.account">
							<el-radio label=""></el-radio>
							<el-radio label=""></el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="">
						<el-radio-group v-model="form.samllChange" @change="changeSmall">
							<el-radio label=""></el-radio>
							<el-radio label=""></el-radio>
						</el-radio-group>
					</el-form-item>
					<el-form-item label="" v-if="isSmall==true">
						<el-radio-group v-model="form.samllChangeType">
							<el-radio label=' ""'></el-radio>
							<el-radio label=' ""'></el-radio>
						</el-radio-group>
					</el-form-item>
					</el-form-item>
				</el-form>
				<div class="save">
			<el-button @click="silverSetSave"></el-button>
		</div>
			</div>
		</div>
		<wechat-set v-if="isOpenWechat" @wechat-click="fromWechat"></wechat-set>
		<alipay-set v-if="isOpenAlipay" @alipay-click="fromAlipay"></alipay-set>
		

	</div>
</template>

<script>
	import wechatSet from "./wechatSet";
	import alipaySet from "./alipaySet";
	import { silverData } from "../api";
	import { isGetRightCfg, silverSet } from "@/config/cfg"
	import { Public } from "@/config/public"
	export default {
		data() {
			return {
				
				activeIndex: "1",
				naveIndex: "",
				isOpenSliver: true,
				isOpenWechat: false,
				isOpenAlipay: false,
				isSmall: false,
				shopId: "",
				form: {
					account: "",
					samllChange: "",
					samllChangeType: "",
					payWay: ""
				},
				payWay: [{
						label: "",
						id: "2"
					},
					{
						label: "",
						id: "3"
					}
				],
				small: [{
						label: ""
					},
					{
						label: ""
					}
				]
			}
		},
		created() {
			this.getShopData();
			let index = sessionStorage.getItem("index")
			if(index!==null) {
				this.activeIndex=index
				this.handleSelect(index)
			}
			this.shopId = Public.getShopid();
		},
		components: {
			wechatSet,
			alipaySet
		},
		methods: {
			//
			getShopData() {
				let data = {
					get_shop_pay_set: 1,
					shop_id:this.shopId
				}
				silverData.getShopData(data, (resp) => {
					this.form.account = this.transToChinese(resp.data.collection_set.is_debt, 1);
					this.form.samllChange = this.transToChinese(resp.data.collection_set.is_mailing, 1);
					this.form.samllChangeType = this.transToChinese(resp.data.collection_set.mailing_type, 0);
					this.isSmall = this.form.samllChange == this.small[silverSet.NO].label?true:false;
				})
			},
			//
			transToChinese(num, status) {
				var name;
				if(status == 1) {
					return name = num == silverSet.NO ? silverSet.code[silverSet.NO] : silverSet.code[silverSet.YES]
				} else if(status == 0) {
					return name = num == silverSet.NO ? silverSet.code[silverSet.NO] : (num == silverSet.YES ? silverSet.code[silverSet.M] : silverSet.code[silverSet.N])
				} else if(status == 3) {
					return name = num == silverSet.code[silverSet.NO] ? silverSet.NO : silverSet.YES
				} else {
					return name = num == silverSet.code[silverSet.M] ? silverSet.YES : silverSet.M

				}

			},
			//
			handleSelect(key) {
				if(key == this.payWay[0].id) {
					this.isOpenWechat = true;
					this.isOpenSliver = false;
					this.isOpenAlipay = false;
				} else if(key == this.payWay[1].id) {
					this.isOpenAlipay = true;
					this.isOpenWechat = false;
					this.isOpenSliver = false;
				} else {
					this.isOpenAlipay = false;
					this.isOpenWechat = false;
					this.isOpenSliver = true;
				}
			sessionStorage.setItem("index",key)
			},
			changeSmall(label) {
				if(this.small[0].label == label) {
					this.isSmall = true;
				} else {
					this.isSmall = false;
				}
			},
			fromWechat() {
				this.isOpenSliver = true;
				this.isOpenWechat = false;
				this.isOpenAlipay = false;
				this.form.payWay = ""
			},
			fromAlipay() {
				this.isOpenSliver = true;
				this.isOpenWechat = false;
				this.isOpenAlipay = false;
				this.form.payWay = ""
			},
			silverSetSave() {
				this.form.account = this.transToChinese(this.form.account, 3);
				this.form.samllChange = this.transToChinese(this.form.samllChange, 3);
				this.form.samllChangeType = this.transToChinese(this.form.samllChangeType, 4);

				let data = {
					save_collection_set: 1, //
					shop_id: this.shopId, //(string)id
					is_debt: this.form.account, //(int)0:,1:
					is_mailing: this.form.samllChange, //(int)0:,1:
					mailing_type: this.form.samllChangeType //(int)(1:,2:
				}
				silverData.silverSet(data, (resp) => {
					if(resp.ret == 0) {
						this.$slnotify({
			            message: ""
			       		});
					}else if(resp.ret==-30034){
						this.$slnotify({
				            message: ""
				       	});
					}else{
					this.$slnotify({
				            message: ""
				       });
					}
					this.getShopData();
				})
			}
		}
	};
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#silver-admin {
		width: 100%;
	}
	
	.form-card {
		color: #000000;
		width: 100%;
		height: 600px;
		background: #FFFFFF;
	}
	
	.save {
		width: 100%;
		background: #FFFFFF;
		margin-top: 200px;
		text-align: center;
	}
	
	.el-menu-demo {
		background: #FFFFFF;
	}
	.form_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		letter-spacing: 0.5px;
		background-color: #F6F8FC;
		padding-left: 14px;
	}
</style>

<style lang="scss">
	@import "src/styles/mixin.scss";
	#silver-admin {
		.el-form {
			margin: 20px 0 0 133px;
			
		}
		.el-form-item {
			width: 100%;
		}
		.el-button {
			@include wh(160px, 40px);
			color: #FFFFFF;
			background: #4877E7;
			border: 1px solid #4877E7;
		}
		.el-radio{
			margin-right: 118px;
		}
		.el-radio-button .el-radio-button__inner {
			width: 120px;
			height: 33px;
			padding: 10px;
			border: none;
			margin-right: 10px;
			border: 1px solid #dedede;
		}
		.el-radio-button:first-child .el-radio-button__inner,
		.el-radio-button:last-child .el-radio-button__inner {
			border-radius: 4px;
		}
		.el-input-group--append .el-input__inner {
			width: 150px;
		}
		.el-input-group__append .el-button {
			width: 55px;
		}
		.el-form-item__label,.el-radio__label{
			opacity: 0.57;
			color: #000000;
		}
	}
</style>


